.header {
  width: 100%;
  height: 100vh;
  background: url("img/preview.png");
  background-size: cover;
  background-position: center;
}

.header-wrapper {
  height: 100%;
  padding-top: 40px;
  width: 100%;
}

.banner {
  margin-right: 25px;
  margin-top: 175px;
  display: flex;
}

.banner__wrapper {
  margin-left: auto;
}

.banner__title {
  font-weight: 600;
  font-size: 38px;
  font-family: 'Montserrat', sans-serif;
  line-height: 48px;
}

.banner__text {
  font-size: 18px;
  margin-top: 40px;
  margin-bottom: 40px;
  padding-left: 5px;
  line-height: 28px;
  color: #787878;
  font-family: 'Nunito Sans', sans-serif;
}

@media (max-width: 991px) {
  .banner {
    margin-top: 120px;
  }
}

@media (max-width: 768px) {
  .header {
    height: 100%;
    background: url("img/preview2.jpg");
    background-size: cover;
    background-position: center;
  }
  .banner {
    align-items: flex-start;
    padding-top: 50px;
    margin-top: 0;
  }
  .banner__wrapper {
    margin: 0 auto;
  }
  .banner__title {
    text-align: center;
  }
  .banner__text {
    text-align: center;
  }
}
